.container-wrapper {
  width: 100%;
  display: flex;
  justify-content: center; }

.container {
  width: 90vw;
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: 200px 200px 200px;
  grid-template-areas: 'profile counter' 'hacks hacks' 'projs projs';
  grid-gap: 20px;
  gap: 20px; }

.profile {
  grid-area: profile;
  background: -webkit-linear-gradient(309.21deg, #E66833 0%, #DC3636 48.53%, #FA9468 100%);
  background: linear-gradient(140.79deg, #E66833 0%, #DC3636 48.53%, #FA9468 100%);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px; }
  .profile .username {
    font-size: 19px;
    line-height: 25px;
    color: #FFFFFF; }
  .profile .description {
    font-size: 11px;
    line-height: 14px;
    text-align: center;
    color: #D7D7D7; }

.counter {
  grid-area: counter;
  text-align: center; }
  .counter .description {
    position: relative;
    top: 10px; }

.list {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 20% auto 15%;
  grid-template-areas: 'title' 'content' 'link'; }
  .list h2 {
    grid-area: title;
    padding-left: 20px; }
  .list .empty {
    grid-area: content;
    display: flex;
    align-items: center;
    justify-content: center; }
    .list .empty span {
      font-size: 19px;
      line-height: 25px;
      color: rgba(0, 0, 0, 0.25); }
  .list a {
    font-size: 12px;
    line-height: 16px;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    color: #6D6D6D;
    grid-area: link;
    text-align: right;
    padding-right: 20px; }
  .list.hacks {
    grid-area: hacks; }
  .list.projects {
    grid-area: projs; }

